.film {
	.film__container {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		gap: var(--espace-2);

		& .film__img {
			aspect-ratio: 3/ 3;
			max-width: max(var(--width-mobile), 50vw);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: center;
			}
		}

		.film__infos {
			display: flex;
			flex-direction: column;
			gap: var(--espace-half);
			padding: var(--espace-1);
			flex: 1;

			.vote {
				display: flex;
				flex-wrap: wrap;
				gap: var(--espace-quart);
				align-items: center;
				height: var(--espace-2);
			}

			.annee {
				font-family: var(--police-texte);
				font-size: var(--text-sm);
				font-weight: normal;
			}
			.genres {
				padding: var(--espace-3quarts) 0;

				.genre {
					display: inline-block;
					border: 1px solid var(--coul-texte);
					border-radius: 40px;
					padding: var(--espace-quart) var(--espace-half);
					margin-right: var(--espace-half);
				}
			}
		}
	}
	.film__commentaires {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		padding: var(--espace-1);
		border-top: 2px solid var(--coul-primary);
		gap: var(--espace-3);
		align-items: flex-start;

		.film__commentaire-list {
			padding: var(--espace-1half) 0;
			display: flex;
			flex-direction: column;
			gap: var(--espace-1);
			flex: 40%;
			/* margin-left: var(--espace-1); */
		}
	}
}
